import { useState } from "react";
import "./Todo.css";
import "react-reveal";
export default function Todo() {
  const [inp, setinput] = useState(" ");
  const [items, setitems] = useState([]);
  const deleteItems = (i) => {};
  return (
    <>
      <input
        value={inp}
        type="text"
        placeholder="ADD ITEMS"
        onChange={(e) => setinput(e.target.value)}
      />

      <button onClick={() => [setitems([...items, inp]), setinput("")]}>
        ADD
      </button>
      {/* <br/> */}
      <button
        onClick={() => {
          setitems([]);
        }}
      >
        {" "}
        CLEAR ALL{" "}
      </button>
      {items.map((v, i) => {
        if (!inp) {
        }

        return (
          <>
            <Fade top>
              <div key={i + 1}>
                <h1> {v} </h1>
                <button onClick={deleteItems}>DELETE ITEM</button>
              </div>
            </Fade>
          </>
        );
      })}
    </>
  );
}
